.modal {
    position: relative;
    max-width: 800px;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2),
      0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12);
    background: #111;
    overflow: hidden;
    border-radius: 8px;
    transition: all 400ms ease-in-out 2s;
    animation: fadeIn 400ms;
  }
  .modal_poster-img {
    width: 100%;
    height: auto;
  }
  .modal_content {
    padding: 40px;
    color: white;
  }
  .modal_title {
    padding: 0;
    font-size: 40px;
    margin: 16px 0;
  }
  .modal_details {
    font-weight: 600;
    font-size: 18px;
  }
  .modal_overview {
    font-size: 15px;
    line-height: 1.5;
  }
  .modal_user-prec {
    color: #46d369;
    padding-right: 10px;
  }
  .modal::-webkit-scrollbar {
    display: none;
    visibility: hidden;
  }
  
  /* Hide scrollbar for IE, Edge and Firefox */
  .modal {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  }
  .wrapper-modal {
    position: fixed;
    inset: 0px;
    background-color: rgb(0 0 0 / 71%);
    -webkit-tap-highlight-color: transparent;
    display: flex;
    justify-content: center;
  }
  .presentation {
    z-index: 1200;
    position: absolute;
  }
  .modal-close {
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
    z-index: 1000;
    color: white;
    border-radius: 50%;
    background: black;
    width: 25px;
    height: 25px;
    text-align: center;
    line-height: 25px;
  }
  @media screen and (max-height: 768px) {
    .wrapper-modal {
      align-items: unset;
      padding-top: 2rem;
    }
    .modal {
      overflow-y: scroll;
    }
  }
  @media screen and (max-width: 768px) {
    .modal_overview {
      font-size: 16px;
    }
    .modal_details {
      font-size: 16px;
    }
    .wrapper-modal {
      padding: 0;
    }
    .modal {
      overflow-y: scroll !important;
    }
  }
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: scale(0.5);
    }
    to {
      opacity: 1;
      transform: scale(1);
    }
  }

.modal_button {
  background-color: white;
  color: black;
  border-radius: 0.2vw;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  height: 40px;
  width: 100px;
}

.modal_button:hover {
  color: #000;
  background-color: rgba(170, 170, 170, 0.9);
  transition: all 0.2s;
}